.Persik {
  display: block;
  width: 30%;
  max-width: 240px;
  margin: 20px auto;
}

.overlow {
    z-index: 2;
    position: fixed;
    width: 100vw;
    height: 100vh;
    visibility: hidden;
    opacity: 0;
    background: rgba(0, 0, 0, .3);
    -webkit-transition: visibility 0.5s linear, opacity 0.5s linear;
    transition: visibility 0.5s linear, opacity 0.5s linear;
}

.menu {
    position: fixed;
    z-index: 2;
    height: calc(100vh - 44px - 20px);
    height: calc(100vh - 44px - constant(safe-area-inset-top) - constant(safe-area-inset-bottom));
    height: calc(100vh - 44px - env(safe-area-inset-top) - env(safe-area-inset-bottom));
    width: 234px;
    background: #87C2E7;
    padding-top: calc(44px + constant(safe-area-inset-top));
    padding-top: calc(44px + env(safe-area-inset-top));
    padding-bottom: constant(safe-area-inset-bottom);
    padding-bottom: env(safe-area-inset-bottom);
    left: -234px;
    -webkit-transition: left 0.5s;
    transition: left 0.5s;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: auto;
}

.menu-dog {
    /* background-image: url('../../img/dog_menu.svg');    */
    background-size: cover;
    width: 100%;
    height: 262px;
}

.menu-item {
    height: 56px;
    border: 1px solid #9DD2F4;
    padding-left: 20px;
    color: #FFF;
    font-family: Gotham Pro;
    font-size: 15px;
    display: flex;
    align-items: center;
}
.header-title {
    display: flex;
    align-items: center;
}
.header-title img {
    margin-right: 10px;
}
@font-face {
    font-family: 'Gotham Pro';
    src: url(/vk-apps-constructor/static/media/GothaProMed.dd3d9ca5.otf);
    font-weight: 500;
    font-style: medium;
}

@font-face {
    font-family: 'Gotham Pro';
    src: url(/vk-apps-constructor/static/media/GothaProReg.aafeb237.otf);
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Gotham Pro';
    src: url(/vk-apps-constructor/static/media/GothaProBol.3f9c5a2b.otf);
    font-weight: bold;
    font-style: normal;
}

body {
    --header_background: #87C2E7; /* цвет шапки */
    --header_alternate_background: #87C2E7; /* цвет статус бара на iOS */
    --background_page: #BEE4F7; /*  */
    /* --background_content: #BEE4F7; */
    --font-android: Gotham Pro; /* основные шпифты для все аппы на android */
    --font-ios: Gotham Pro; /* основные шпифты для все аппы на iOS */
    overflow: hidden!important;
}

/* Уберает линию после шапки на iOS */
.PanelHeader-bg::after {
    height: 0!important;
}
